body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.px1 {
  padding-left: 1rem;
  padding-right: 1rem;
}

.mt2 {
  margin-top: 2rem;
}

.mt5 {
  margin-top: 13rem;
}

/* label color */
.input-field label {
  color: rgb(191, 126, 255);
}
/* label focus color */
input[type="text"]:not(.browser-default):focus:not([readonly]) + label {
  color: rgb(156, 35, 255);
}
/* label underline focus color */
input[type="text"]:not(.browser-default):focus:not([readonly]) {
  border-bottom: 1px solid rgb(156, 35, 255);
  box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.147);
}

.todo {
  margin-bottom: 1rem;
}

.todo.completed span {
  text-decoration: line-through;
}

.todo label {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  padding: 1rem;
  background: #f3f3f3;
}

.todo i {
  z-index: 2;
}

.btn {
  margin: 1rem auto;
  width: 20%;
}

.isActive {
  color: red;
}

.title {
  font-weight: 600;
}

.col {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.text {
  font-size: 20px;
}

